// Decorador injectable
import {Injectable} from "angular2/core";

@Injectable()
export class SessionService {
  private _db: any = window.sessionStorage.getItem && window.sessionStorage;

  public getSession(key: string): number | boolean {
    var _db: any = this._db,
        _currentSession: number;


    if (_db) {
      _currentSession = _db.getItem(key);

      if (_currentSession !== null) {
        return _currentSession;
      } else {
        return false;
      }

    }

    return false;


  }

  public addSession(key: string, userId: number): boolean {
    var _db: any = this._db;

    if (_db) {
      _db.setItem(key, userId);
      return true;
    }

    return false;

  }

  public removeSession(key:string):boolean {
    var _db: any = this._db,
        _oldSession:any = null;

    if (_db) {
      _db.removeItem(key);
      _oldSession = _db.getItem(key);

      if ( _oldSession === null ) {
        return true;
      } else {
        return false;
      }

    }

    return false;
  }
}