// Decorador injectable
import {Injectable} from "angular2/core";

// Modelo de usuario
import {User} from "../models/user";

// Usuarios falsos
import {USERS} from "../models/mock.user";

@Injectable()
export class UserService {
  private users: User[] = USERS;
  public currentUser: User;
  private _dbName: string = "users";
  private _db: any = window.localStorage.getItem && window.localStorage;

  public getUsers(): any {
    var _dbName: string = this._dbName,
        _db: any = this._db,
        _USERS: any = _db.getItem(_dbName);

    if( _USERS !== null ) {
      return Promise.resolve( JSON.parse( _USERS ) )

    } else {
      return Promise.resolve( this.users );

    }

  }

  public getUserBy( list:any[], key: string, value:string | number ): any {
    var _currentUser: User;

    list.map((elem, index) => {

      if ( elem[key] == value ) {
        _currentUser = elem;
      }

    });

    if( _currentUser ) {
      return _currentUser;
    }

    return false;
  }

  public updateUserById( users: User[], updatedUser: User): boolean {
    var _result: User[],
        _users= users,
        _db: any = this._db,
        _dbName: string = this._dbName;

    console.log(_users);
    _users.map((elem, index) => {

      if ( elem["id"] == updatedUser["id"]) {
        _users[index] = updatedUser;
      }

    });


    _result = _users;

    if( _result && _result.length && (_db) ) {
      let _success = _db.setItem( _dbName, JSON.stringify(_result) );

      if( _success !== null ) {
        return true;
      }
    }

    return false;
  }

  public addUser( newUser:User ): boolean {
    var _dbName: string = this._dbName,
        _db: any = this._db,
        _USERS: User[] = this.users,
        oldUsers = _db.getItem(_dbName);

    if( _db ) {

      if (oldUsers !== null) {
        _USERS = JSON.parse(oldUsers);
        _USERS.push(newUser);

      } else{
        _USERS.push(newUser);
      }

      console.log(_USERS);
      _db.setItem(_dbName, JSON.stringify(_USERS));
      return true;

    } else {
      return false;
    }

  }

  private getCurrentUser(callbacks): any {
    var _users = this.getUsers(),
        _currentUser: User = this.currentUser;


    if( typeof _currentUser != "undefined" && typeof callbacks == "undefined" ) {
       return _currentUser;
    } else {
      _users.then(callbacks);

    }

  }


}