// Decorador Component
import {Component} from "angular2/core";

// http://blog.thoughtram.io/angular/2015/09/17/resolve-service-dependencies-in-angular-2.html
import {Inject} from "angular2/core";

// Directivas: ROUTER_DIRECTIVES y validación de formularios, ademas de Router
import {ROUTER_DIRECTIVES, Router, RouteParams} from "angular2/router";
import {NgForm} from "angular2/common";

// Modelo user
import {User} from "../../models/user";

// Servicio que administra los usuarios
import {UserService} from "../../services/user.service";
import {SessionService} from "../../services/session.service";

@Component({
  selector: "session",
  templateUrl: "/templates/session/session.component.html",
  directives: [NgForm, ROUTER_DIRECTIVES],
  providers: [UserService, SessionService]
})

/**
 * Pasos para iniciar sesión:
 * 1) Validar formulario
 * 2) Comprobar que usuario exista
 * 3) Agregar una cookie con id de usuario en caso que sea verdad lo anterior,
 * redirigir a aplicación TODOAPP
 */


export class SessionComponent {
  public user: any = {};
  public submitted: boolean;
  public error: boolean;

  constructor( @Inject(UserService) private _userService: UserService, @Inject(SessionService) private _sessionService: SessionService, @Inject(Router) private _router: Router,  private _routeParams: RouteParams) {

  }

  ngOnInit() {
    let logout = this._routeParams.get("logout");

    if( logout !== null && logout == 1 ) {
      console.log("Logout");
      this.logOut();
    }
  }

  public onSubmit() {
    var _users = this._userService.getUsers(),
        _getUserBy = this._userService.getUserBy,
        _user = this.user;


    _users.then((result) => {
      var currentUser = _getUserBy(result, "email", _user.email);

      this.logIn(currentUser);
      return true;

    }, (result) => {

      console.warn("Error getting users");
      console.log(result);

    });
  }

  private logIn(currentUser: User | boolean): boolean {
    var _user = this.user;

    if (typeof currentUser == "object" && (currentUser.password == _user.password) ) {
      this.logOut();
      this._sessionService.addSession("currentUser", currentUser.id);

      console.log(this);
      console.info("added session");
      this._router.navigate(["User", { id: parseInt(currentUser.id) }]);
      return true;
    }

    this.error = true;
    console.warn("Passwords does not match");
    return false;

  }

  private logOut() {
    this._sessionService.removeSession("currentUser");
  }
}