import {Component, Inject, Input, Output, EventEmitter} from "angular2/core";

// Models
import {User} from "../../models/user";
import {Todo} from "../../models/todo";

// Services
import {IdGeneratorService} from "../../services/id-generator.service";
import {UserService} from "../../services/user.service";

// Pipes
import {TodoListFilterPipe} from "../../pipes/todo-list-filter-pipe";

@Component({
  selector: "todo",
  templateUrl: "/templates/todo/todo.component.html",
  pipes: [TodoListFilterPipe],
  providers: [IdGeneratorService]
})

export class TodoComponent {
  public newTodo: string;
  public defaultStatus: string = "active";
  public filterStatus: string = "all";
  @Input() public todos: Todo[];
  @Output() public saveTodos = new EventEmitter();

  constructor(private _idGeneratorService: IdGeneratorService, private _userService: UserService) { }

  addTodo():void {
    var newID = this._idGeneratorService.newID,
        /**
         * Estatus por defecto para las todos
         * @type {string}
         */
        defaultStatus: string = this.defaultStatus;
    /**
     * Comprobando que el input no esté vacio
     */
    if( this.newTodo && this.newTodo.length > 0 ) {
      let newTodo: Todo = {
        id: newID(),
        content: this.newTodo,
        status: defaultStatus
      };

      /**
       * Agregando la nueva tarea al modelo todos
       */
      this.todos.push(newTodo);

      /**
       * Emitiendo evento para guardar la lista de TODOS en la "base de datos"
       */
      this.saveTodos.emit();

      /**
       * Reseteando el modelo del input
       * @type {String}
       */
      this.newTodo = "";

      /**
       * Modificando el filtro para que se muestren todas las tareas
       * @type {String}
       */
      this.filterStatus = "active";
    }

  }

  removeTodo(todo: Todo):void {

    this.todos.filter( (elem, index) => {
      if( elem["id"] == todo["id"] ) {
        this.todos.splice(index, 1)
        this.saveTodos.emit();
        console.warn("Atención, será eliminado");
      }
    });


  }

  toggleStatus(todo: Todo):void {
    var _filterStatusBackup: string = this.filterStatus+"";

    if ( todo.status == "active" ) {
      todo.status = "completed";
    } else {
      todo.status = "active";
    }

    /**
     * Emitiendo evento para guardar la lista de TODOS en la "base de datos"
     *
     */
    this.saveTodos.emit();
  }

  toggleFilter( newStatus: string ):void {
    this.filterStatus = newStatus;
  }
}