// Core
import {Component} from "angular2/core";

// Routing: Decorador y Directivas (e.j routerLink)
import {RouteConfig, ROUTER_DIRECTIVES} from "angular2/router";

// Componentes
import {SessionComponent} from "./components/session/session.component";
import {RegistrationComponent} from "./components/registration/registration.component";
import {UserComponent} from "./components/user/user.component";

@Component({
  selector: "app",
  templateUrl: "/templates/app/app.component.html",
  directives: [ROUTER_DIRECTIVES]
})

@RouteConfig([
  { path: "/", name: "Session", component: SessionComponent, useAsDefault: true },
  { path: "/register", name: "Registration", component: RegistrationComponent },
  { path: "/user/:id", name: "User", component: UserComponent }
])


export class AppComponent {}