import {Component} from "angular2/core";

// http://blog.thoughtram.io/angular/2015/09/17/resolve-service-dependencies-in-angular-2.html
import {Inject} from "angular2/core";

// Modelos
import {User} from "../../models/user";
import {Todo} from "../../models/todo";
import {TODOS} from "../../models/mock.todo";

// Servicios
import {UserService} from "../../services/user.service";
import {SessionService} from "../../services/session.service";

// Componentes
import {TodoComponent} from "../../components/todo/todo.component";

// Routing: Directivas (e.j routerLink)
import {ROUTER_DIRECTIVES} from "angular2/router";

@Component({
  selector: "user",
  templateUrl: "/templates/user/user.component.html",
  directives: [ROUTER_DIRECTIVES, TodoComponent],
  providers: [UserService, SessionService]
})

export class UserComponent {
  public title: string = "Add todo";
  public user: User;
  public flash: string;

  constructor( @Inject(UserService) private _userService: UserService, @Inject(SessionService) private _sessionService: SessionService) { }


  ngAfterViewInit() {
    /**
     * Obteniendo usuarios luego de inicializada la vista
     */
    this._userService.getCurrentUser((result) => {
      let _currentUserId: number = parseInt( this._sessionService.getSession("currentUser") );

      console.log(result);
      this._userService.currentUser = this._userService.getUserBy(result, "id", _currentUserId);

      /**
       * Agregando tareas de prueba en caso de no existir en el usuario actual
       */
      if ( !this._userService.currentUser.hasOwnProperty("todos") ) {
          this._userService.currentUser.todos = TODOS;
      }

      this.user = this._userService.currentUser;

      this.setTitle();

    }, (result) => {
        console.error("Error getting users");
        console.log(result);

    });
  }

  setTitle() {
    var name = this.user.name;
    this.title = `Hello ${name}, please add a todo`;
  }

  public saveTodos() {
    var _currentUser: User = this._userService.currentUser,
        _users = this._userService.getUsers(),
        _updatedUsers: boolean;



    _users.then((result) => {
      _updatedUsers = this._userService.updateUserById( result, _currentUser );

      console.group("Save todos");
        console.log(_updatedUsers);
        console.log("emitter");
      console.groupEnd();

    }, (result) => {
      console.error("Error getting users");
      console.log(result);
    })


  }


}