// Decorador Component
import {Component} from "angular2/core";

// http://blog.thoughtram.io/angular/2015/09/17/resolve-service-dependencies-in-angular-2.html
import {Inject} from "angular2/core";

// Directivas: Routing y validación de formularios
import {ROUTER_DIRECTIVES} from "angular2/router";
import {NgForm} from "angular2/common";

// Modelo de usuario
import {User} from "../../models/user";

// Servicio que maneja la adicción de usuario a la "DB"
import {UserService} from "../../services/user.service";

// Generador de id para usuario
import {IdGeneratorService} from "../../services/id-generator.service";

@Component({
  selector: "registration",
  templateUrl: "/templates/registration/registration.component.html",
  directives: [NgForm, ROUTER_DIRECTIVES],
  providers: [UserService, IdGeneratorService]
})

/**
 * Pasos para "registrar" a un nuevo usuario:
 * 1) Validar información del usuario
 * 2) Comprobar que el usuario no esté registrado en la "DB".
 * 3) Agregar el usuario a la DB con un nuevo ID y redirigir al login con un mensaje de exito.
 * 4) En caso contrario no agregar y mostrar mensaje de fallo.
 *
 */
export class RegistrationComponent {
  public newUser:any = {};
  public submitted: boolean;
  public active = true;

  constructor( @Inject(UserService) private _userService: UserService, @Inject(IdGeneratorService) private _idGeneratorService: IdGeneratorService) { }

  onSubmit() {
    console.log("Hola");
    let _newId = this._idGeneratorService.newID(),
        _newUser: User = {
          "id": _newId,
          "name": this.newUser["name"],
          "email": this.newUser["email"],
          "password": this.newUser["password"]
        },
        userAdded = ( _newUser.hasOwnProperty("name") && _newUser.name.length > 0 ) && this._userService.addUser(_newUser);

    console.log(_newUser);
    if( userAdded ) {
      this.newUser = {};
      this.submitted = true;
      // this.active = false;
      // setTimeout(() => { this.active = true }, 0);
    }

  }
}